import Util from '../util';

class Level {
    constructor(context) {
        this.context = context;
        this.pathStart = {x: 0, y: 0};
        this.pathX = 0;
        this.pathY = 0;
        this.pathSize = 100;
        this.xBound = this.context.canvas.width - this.pathSize;
        this.yBound = this.context.canvas.height - this.pathSize;
        this.path = [this.pathStart];
    }

    drawLevel() {
        this.context.beginPath();
        this.context.rect(this.pathStart.x, this.pathStart.y, 100, 100);
        this.context.fillStyle = "#e83030";
        this.context.fill();
    }

    pathGenerator() {
        while (this.pathX < this.xBound && this.pathY < this.yBound) {
            if (Util.randomNumber(0,1) === 0) {
                this.pathX += this.pathSize;
            } else {
                this.pathY += this.pathSize;
            }
        }
    }

    validPath(x, y) {
        if (this.pathX < x && x < this.xBound && this.pathY < y && this.pathY < this.yBound) {
            return true;
        }
        return false;
    }
    
}

export default Level;